@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block; }

audio,
canvas,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden], template {
  display: none; }

html {
  background: #fff;
  color: #000;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

html,
button,
input,
select,
textarea {
  font-family: sans-serif; }

body {
  margin: 0; }

a {
  background: transparent; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

h2 {
  font-size: 1.5em;
  margin: 0.83em 0; }

h3 {
  font-size: 1.17em;
  margin: 1em 0; }

h4 {
  font-size: 1em;
  margin: 1.33em 0; }

h5 {
  font-size: 0.83em;
  margin: 1.67em 0; }

h6 {
  font-size: 0.75em;
  margin: 2.33em 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: "\201C" "\201D" "\2018" "\2019"; }

q:before, q:after {
  content: '';
  content: none; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 0; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0;
  white-space: normal; }

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  vertical-align: baseline; }

button, input {
  line-height: normal; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
input[disabled] {
  cursor: default; }

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }
  input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none; }

input[type="checkbox"], input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

.container {
  max-width: 1440px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto; }

#main {
  font-family: "Roboto", sans-serif; }
  #main .img-content {
    padding: 96px 0;
    display: flex;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between; }
    @media (max-width: 992px) {
      #main .img-content {
        flex-direction: column;
        -webkit-flex-direction: column; } }
    #main .img-content__left {
      min-width: 505px;
      max-height: 330px; }
      @media (max-width: 992px) {
        #main .img-content__left {
          min-width: 100%; } }
      #main .img-content__left img {
        width: 100%;
        height: 330px;
        object-fit: cover; }
    #main .img-content__right {
      max-width: 380px;
      margin: 0 auto 0 95px; }
      @media (max-width: 992px) {
        #main .img-content__right {
          margin: 0 auto;
          max-width: 505px; } }
      #main .img-content__right h1 {
        font-size: 42px;
        font-weight: 700;
        color: #121212;
        margin-bottom: 24px; }
      #main .img-content__right p {
        font-size: 16px;
        line-height: 24px;
        font-weight: 300;
        color: #121212; }

#close-btn {
  display: none; }
  #close-btn.active {
    display: block; }

.btn {
  background-color: #E4C1AE;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  border-radius: 32px;
  padding: 16px 40px;
  display: inline-block;
  margin-top: 32px;
  text-decoration: none;
  border: 1px solid transparent;
  transition: .5s all ease-in; }
  .btn:hover {
    border: 1px solid #E4C1AE;
    color: #E4C1AE;
    background-color: transparent; }

#popup {
  background: rgba(12, 12, 12, 0.75);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999 !important;
  display: none;
  box-sizing: border-box; }
  #popup.active {
    display: block; }
  @media (max-width: 992px) {
    #popup {
      padding: 0 20px; } }
  #popup .popup-item {
    position: fixed;
    float: left;
    left: 50%;
    top: 50%;
    z-index: 100000;
    background-color: #fff;
    transform: translate(-50%, -50%);
    transition: .5s;
    max-width: 920px;
    padding: 56px 95px;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    justify-content: space-between;
    -webkit-justify-content: space-between; }
    @media (max-width: 992px) {
      #popup .popup-item {
        position: relative;
        flex-direction: column;
        -webkit-flex-direction: column;
        align-items: center;
        -webkit-align-items: center;
        padding: 50px; } }
    #popup .popup-item__close {
      cursor: pointer;
      display: block;
      width: 16px;
      position: absolute;
      top: 30px;
      right: 23px;
      height: 16px; }
      #popup .popup-item__close span {
        width: 15px;
        height: 2px;
        background-color: #000;
        display: block;
        top: 8px;
        transform: rotate(45deg);
        position: absolute;
        border-radius: 6px; }
        #popup .popup-item__close span::after {
          content: '';
          display: block;
          width: 15px;
          height: 2px;
          background-color: #000;
          position: absolute;
          top: 0px;
          right: 0px;
          transform: rotate(-90deg);
          border-radius: 6px; }
    #popup .popup-item__left h2 {
      font-size: 42px;
      font-weight: 700;
      color: #121212; }
    #popup .popup-item__left p {
      font-size: 16px;
      font-weight: 300;
      color: #121212; }
      #popup .popup-item__left p span {
        font-weight: 700; }
    #popup .popup-item__right {
      align-self: flex-end; }
      @media (max-width: 992px) {
        #popup .popup-item__right {
          align-self: center; } }
